import { render } from '@testing-library/preact';
import { expect } from 'chai';
import { Text } from '../Text';
/*TO DO: Use computed style instead of classList length. */
describe('Test Text component', () => {
  it('default text', async function () {
    const content = render(<Text>Hello world</Text>);
    const textSpan = await content.findByText('Hello world');
    expect(textSpan).not.null;
  });
  it('lineClamp applies css class and lineClamp value', async () => {
    const content = render(<Text lineClamp={4}>Hello world</Text>);
    const firstElement = content.container.firstElementChild;
    const lineClampValue = (firstElement as HTMLElement).style.webkitLineClamp;
    // @ts-ignore
    const classList = firstElement.classList;
    expect(classList.length).equals(6);
    expect(lineClampValue).equals('4');
  });
  it('truncation applies css class', async () => {
    const content = render(<Text truncation="ellipsis">Hello world</Text>);
    const firstElement = content.container.firstElementChild;
    // @ts-ignore
    const classList = firstElement.classList;
    expect(classList.length).equals(6);
  });

  it('has id property', async function () {
    const content = render(<Text id="textId">Test Text</Text>);
    const text = await content.findByText('Test Text');
    const id = text.parentElement?.getAttribute('id');
    expect(id).to.equal('textId');
  });
});
